#TopbarView{display:flex;padding:20px 0;justify-content:space-between}#TopbarView.TOOLBAR_INNER_TYPE{background:#fff;border-bottom:1px solid #eceeef}#TopbarView #app-logo{cursor:pointer;padding-left:20px}#TopbarView #app-logo img{height:40px}#TopbarView #app-menu-launcher{visibility:hidden}#TopbarView #app-menu{padding:0px}#TopbarView #app-menu ul{display:flex;list-style:none;padding:0px}#TopbarView #app-menu ul li{cursor:pointer;color:#fff;padding:5px 0px;margin:0 20px;border-bottom:2px solid transparent}#TopbarView #app-menu ul li:hover{border-bottom:2px solid #00A550}#TopbarView #app-menu ul md-icon{font-size:1.25rem;opacity:0.7}@media (max-width: 767px){#TopbarView{background:#fff;flex-wrap:wrap}#TopbarView #app-logo img{height:30px}#TopbarView #app-menu,#TopbarView #app-menu-launcher{visibility:visible}#TopbarView #app-menu-launcher{cursor:pointer;padding-right:20px}#TopbarView #app-menu{width:100%;height:0px;overflow:hidden}#TopbarView #app-menu ul{flex-direction:column;padding:0px}#TopbarView #app-menu ul li{margin:0;color:#161d1e;padding:10px 20px;border:none}#TopbarView #app-menu ul li:first-child{margin-top:20px}#TopbarView #app-menu ul li:hover{background:#f7f7f9;border:none}#TopbarView #app-menu md-icon{visibility:hidden}}
